
@media (max-width: 600px) {
.header-temp {
    min-height: 10%;
    flex-direction: column;
}
.secondary {
    min-height: 90%;
}
.sidebar-temp {
    position: fixed;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    z-index: 2;
}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.wrapper {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
}
.wrapper * {
  box-sizing: border-box;
}
.header-temp {
  width: 100%;
  height: 7.5%;
  background-color: #2a56c6;
  font-family: 'Roboto', sans-serif;
  font-size: 2vh;
  display: flex;
}
.secondary {
  display: inline-flex;
  width: 100%;
  height: 92.5%;
}
.sidebar-temp {
  flex: 1;
}
.main-temp {
  flex: 6;
}
input {
  color: black;
}
div.circle {
  position: fixed;
  z-index: 1;
  bottom: 0px;
  right: 0px;
  margin: 25px;
  background: fuchsia;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  vertical-align: middle;
  text-align: center;
}
a.circle {
  background-color: transparent;
}
i.circle {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  color: white;
}
.circle:hover {
  cursor: pointer;
}
.fade-enter-active, .fade-leave-active {
  transition: all .5s linear;
}
.fade-enter, .fade-leave-to {
  transform: translateX(-100%);
  opacity: 0;
}

li a {
  display: block; /* Make Sidebar buttons display in a column */
  padding: 12px; /* Add some padding */
  text-align: left;
}
a, a:visited, a:hover, a:active {
  text-decoration: none; /* Remove underline from links */
  color: inherit; /* Remove original hyperlink color */
}
.sidebar li {
  font-family: 'Roboto', sans-serif; /* Google's font from their public library */
  color: black;
}
.sidebar li:hover {
  background-color: #ccc; /* Dark grey background on mouse-over */
}
.router-link-active {
  color: #2962ff;
  background-color: #ccc; /* Dark grey background on active link */
}
.sidebar {
  position: relative;
  height: 100%;
  background-color: white;
  border-right: 1px solid grey;
}
.sidebar ul{
  padding: 0;
  margin: 0;
  overflow: hidden; /* Remove default unordered list formatting */
}
.sidebar ul li i{
  margin-right: 15px; /* Add 15px of space between Font Awesome icons and text */
}
@media (max-width: 600px) {
.sidebar {
    width: 75vw;
    z-index: 2;
}
.sidebar-overlay {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1;
}
.fade-enter-active, .fade-leave-active {
    transition: all .5s linear;
}
.fade-enter, .fade-leave-to {
    transform: translateX(-100%);
    opacity: 0;
}
}

.topbar-row {
  display: flex;
  flex: 1;
  height: 100%;
}
.menu {
  display: flex;
  align-items: center;
}
.menu i {
  position: relative;
  color: white;
  cursor: pointer;
  padding: 10px;
}
.title {
  display: flex;
  align-items: center;
  color: white;
  font-size: 4vmin;
}
.search {
  display: flex;
  flex: 1;
  align-items: center;
  color: darkgray;
}
.search input {
  width: 97%;
  height: 75%;
  border-radius: 5px;
  padding-left: 5px;
  margin-left: 5px;
  margin-right: 5px;
}
.search .fa {
  margin: auto -30px;
}
.language {
  flex: 1;
  text-align: right;
  color: black;
  padding: 5px;
}
@media (max-width: 600px) {
.title {
    font-size: 5vmin !important;
}
.language {
    position: absolute;
    top: 5px;
    right: 5px;
    font-size: 3vmin;
    border-radius: 3px;
    background: white;
    padding: 0 !important;
}
}

#contacts {
  text-align: left;
  padding-left: 1em;
}
.flag-icon {
  background-size: 110%;
  border: 1px solid black;
}
th {
  cursor: pointer;
}
th:hover {
  background-color: #e5efff;
}
.profile-picture {
  border-radius: 50%;
}
.contact-list tr:nth-child(even) {
  background-color: #fcfcfc
}
.contact-list > tbody > tr > td {
  vertical-align: middle;
}
.contact-avatar{
  width: 80px;
}
.contact-name{
  width: 140px;
}
.contact-gender{
  width: 80px;
}
.contact-country{
  width: 80px;
}
.contact-email{
  width: 240px;
}
.contact-postal{
  width: 120px;
}
.contact-edit{
  width: 100px;
}
td {
  word-wrap:break-word;
}
table {
  table-layout:fixed;
}
@media (max-width: 600px) {
.contact-list > tbody > tr > td {
    padding: 3px;
}
th {
    font-size: 10px;
}
.contact-avatar{
    display:none;
}
td {
    border-left: 1px gray;
    font-size: 3vmin;
}
#contacts{
    padding-left: 5px;
}
.contact-name{
    width: 130px;
}
.contact-gender{
    width: 60px;
}
.contact-country{
    display: none;
}
.contact-email{
    width: 100px;
}
.contact-postal{
    display: none;
}
.contact-list {
    width: auto;
}
}

.modal-background {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-main {
  background: #fff;
  box-shadow: 2px 2px 20px 1px;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
}
.modal-head,
.modal-foot {
  padding: 15px;
  display: flex;
}
.modal-head {
  border-bottom: 1px solid #eee;
  color: #4aae98;
  justify-content: space-between;
}
.modal-foot {
  border-top: 1px solid #eee;
  justify-content: flex-end;
}
.modal-soul {
  position: relative;
  padding: 20px 10px;
}
.btn-close {
  border: none;
  font-size: 20px;
  /* padding: 20px; */
  cursor: pointer;
  font-weight: bold;
  color: #4AAE9B;
  background: transparent;
}
.btn-green {
  color: white;
  background: #4AAE9B;
  border: 1px solid #4AAE9B;
  border-radius: 2px;
}

/* transitions */
.modal-fade-enter,
.modal-fade-leave-active {
  opacity: 0;
}
.modal-fade-enter-active,
.modal-fade-leave-active {
  transition: opacity .5s ease;
}

.country-dropdown {
  text-align: center;
}
label {
  padding: 5px;
}

p[data-v-cab9e712] {
  margin: 10px;
  text-align: left;
}

